.add-note-btn {
	color: #aaf;
	top: 0;
	left: 0;
	-webkit-transform: translate(0, 100%);
	    -ms-transform: translate(0, 100%);
	        transform: translate(0, 100%);
	position: absolute;
	display: block;
	font-size: 3em;
	width: 3em;
	padding: .1em .25em;
	text-align: center;
	cursor: pointer;
	border: thin solid #002;
	background: #004;
}



